<template>
  <div>
    <button @click="startGame" v-if="!gameStarted">Начать игру</button>
    <div class="game-board">
      <div v-for="(row, i) in field" :key="i" class="row">
        <div v-for="(cell, j) in row" :key="j" class="cell" @click="checkField(i, j)" :class="{ open: cell.opened }">
          <span v-if="cell.opened">{{ cell.value }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Web3 from 'web3';
const web3 = new Web3(new Web3.providers.HttpProvider('http://127.0.0.1:8545'));
const abi = [
        {
            "constant": false,
            "inputs": [],
            "name": "startGame",
            "outputs": [
              {
                "name": "",
                "type": "uint8"
              }
            ],
            "payable": false,
            "stateMutability": "nonpayable",
            "type": "function"
          },
          {
            "constant": false,
            "inputs": [
              {
                "name": "endStr",
                "type": "string"
              }
            ],
            "name": "endGame",
            "outputs": [
              {
                "name": "",
                "type": "uint8"
              }
            ],
            "payable": false,
            "stateMutability": "nonpayable",
            "type": "function"
          },
          {
            "constant": false,
            "inputs": [
              {
                "name": "_i",
                "type": "uint8"
              },
              {
                "name": "_j",
                "type": "uint8"
              }
            ],
            "name": "bombActivate",
            "outputs": [
              {
                "name": "",
                "type": "uint8"
              }
            ],
            "payable": false,
            "stateMutability": "nonpayable",
            "type": "function"
          },
          {
            "constant": true,
            "inputs": [
              {
                "name": "_i",
                "type": "uint8"
              },
              {
                "name": "_j",
                "type": "uint8"
              }
            ],
            "name": "checkField",
            "outputs": [
              {
                "name": "",
                "type": "uint8[100]"
              }
            ],
            "payable": false,
            "stateMutability": "view",
            "type": "function"
          },
          {
            "constant": true,
            "inputs": [],
            "name": "getStatusGame",
            "outputs": [
              {
                "name": "",
                "type": "uint8"
              }
            ],
            "payable": false,
            "stateMutability": "view",
            "type": "function"
          },
          {
            "constant": true,
            "inputs": [],
            "name": "getEndStr",
            "outputs": [
              {
                "name": "",
                "type": "string"
              }
            ],
            "payable": false,
            "stateMutability": "view",
            "type": "function"
          },
          {
            "constant": false,
            "inputs": [],
            "name": "getRandom",
            "outputs": [
              {
                "name": "",
                "type": "uint8"
              }
            ],
            "payable": false,
            "stateMutability": "nonpayable",
            "type": "function"
          }
];
const contractAddress = '0xe7f1725e7734ce288f8367e1bb143e90bb3f0512';
const contract = new web3.eth.Contract(abi, contractAddress);

export default {
  data() {
    return {
      gameStarted: false,
      field: Array.from({length: 10}, () => Array.from({length: 10}, () => ({ opened: false, value: 0 }))),
    };
  },
  methods: {
    async startGame() {
      try {
        const result = await contract.methods.startGame().send({ from: '0xf39Fd6e51aad88F6F4ce6aB8827279cffFb92266' });
        console.log('Транзакция успешно отправлена:', result);
        this.gameStarted = true;
      } catch (error) {
        console.error('Ошибка при выполнении запроса startGame:', error);
      }
    },
    async checkField(i, j) {
      try {
        const fieldState = await contract.methods.checkField(i, j).call();
        const fieldStateString = fieldState.map((value) => Number(value));
        console.log('Транзакция успешно отправлена:',fieldStateString);

        this.field[i][j].opened = true;
        this.field[i][j].value = fieldStateString;
      } catch (error) {
        console.error('Ошибка при выполнении запроса checkField:', error);
      }
    },
  },
};
</script>

<style>
.game-board {
  display: flex;
  flex-wrap: wrap;
  width: 20cm;
}

.row {
  display: flex;
  flex-basis: 20%;
}

.cell {
  width: 2cm;
  height: 2cm;
  margin: 0.1cm;
  background-color: gray;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.open {
  background-color: white;
}
</style>